.logo-header {
  background-color: #000000;
}

.header {
  background-color: #000000;
}
@media (min-width: 768px) {
  .header.fijo {
    z-index: 1;
    left: 0;
    top: 0;
    position: fixed;
    width: 100%;
    box-shadow: 0.1rem 0.2rem 0.1rem #333;
  }
}
.header .contenido-header h1 {
  color: #FFFFFF;
  text-align: center;
  margin: 0;
  padding: 1rem 0 3rem 0;
  font-family: "Shadows Into Light", cursive;
  font-weight: 400;
}
@media (min-width: 768px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-between;
  }
}

.logo {
  display: flex;
  justify-content: center;
}
.logo img {
  width: auto;
}

.navegacion-principal .navegacion-enlace:last-of-type {
  padding-bottom: 2rem;
}
.navegacion-principal a {
  display: block;
  text-align: center;
  color: #FFFFFF;
  font-weight: bold;
  padding: 1rem;
}
.navegacion-principal a:hover {
  background-color: #383838;
  border-radius: 2rem;
}
@media (min-width: 768px) {
  .navegacion-principal {
    display: flex;
    gap: 1rem;
    align-items: center;
  }
  .navegacion-principal .navegacion-enlace:last-of-type {
    padding-bottom: 1rem;
  }
}

.canciones {
  z-index: 0;
  position: relative;
  margin-top: 3rem;
  margin-bottom: 3rem;
  max-width: 90%;
  max-height: 80rem;
  min-height: 50rem;
}
.canciones .overlay {
  background-image: url(../../../gifs/omori-start-screen.gif);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.titulo-canciones {
  padding: 5rem 0;
  margin-bottom: 0rem;
}
.titulo-canciones h2 {
  background-color: rgba(8, 1, 1, 0.2);
  border-radius: 1rem;
  padding: 0.5rem;
  color: #FFFFFF;
  text-align: center;
  font-family: "Montserrat", sans-serif;
  font-weight: bold;
  font-size: 3rem;
  margin: 0;
}

.botones-canciones {
  display: block;
}
.botones-canciones button {
  display: block;
  word-wrap: break-word;
  width: 100%;
  margin-bottom: 2rem;
  background-color: rgba(69, 231, 231, 0.767);
  padding: 0.5rem;
  color: #FFFFFF;
  font-weight: bold;
  border-radius: 2rem;
}
@media (min-width: 468px) {
  .botones-canciones {
    display: flex;
    justify-content: space-around;
  }
  .botones-canciones button {
    margin-bottom: 0;
    width: 20%;
  }
}

.pausar {
  padding: 2rem 0;
}
.pausar button {
  display: block;
  word-wrap: break-word;
  width: 100%;
  background-color: rgba(255, 0, 85, 0.468);
  padding: 1rem;
  color: #FFFFFF;
  font-weight: bold;
  border-radius: 2rem;
}

.galeria {
  padding: 5rem 0;
}
.galeria h3 {
  text-align: center;
  text-transform: uppercase;
  font-family: "Shadows Into Light", cursive;
}

.galeria-imagenes {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 468px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(9, 1fr);
    gap: 2rem;
  }
}
.galeria-imagenes .imagen {
  cursor: pointer;
}

.overlay {
  z-index: 1;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}
.overlay picture {
  display: block;
  max-width: 80rem;
}

.Black-space {
  padding: 2rem 0;
  background-color: #000000;
  border-radius: 2rem;
  margin-bottom: 3rem;
}
.Black-space h1 {
  text-align: center;
  font-family: "Shadows Into Light", cursive;
  color: #FFFFFF;
}
.Black-space p {
  text-align: center;
  font-size: 2rem;
  font-family: "Shadows Into Light", cursive;
  color: rgb(54, 6, 30);
  font-weight: bold;
}

.puertas {
  display: block;
}
@media (min-width: 768px) {
  .puertas {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 3rem;
  }
}

.bloque-gifs {
  background-color: #FFFFFF;
}
.bloque-gifs h1 {
  margin: 0;
  padding-top: 2rem;
  text-align: center;
  font-family: "Shadows Into Light", cursive;
}

.gifs {
  margin-left: 1rem;
  margin-right: 1rem;
}
@media (min-width: 768px) {
  .gifs {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 3rem;
  }
}

.gif {
  margin: 3rem 0;
  position: relative;
  overflow: hidden;
}
.gif .overlay-gif {
  position: absolute;
  background: rgb(195, 34, 184);
  background: linear-gradient(0deg, rgba(195, 34, 184, 0.5133403703) 0%, rgba(45, 253, 229, 0.4657213227) 100%);
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.contenido-gifs {
  background-color: rgba(8, 1, 1, 0.2);
  padding: 0.5rem 0;
}
.contenido-gifs p {
  margin: 0;
  font-size: 1.5rem;
  text-transform: uppercase;
  text-align: center;
  color: #FFFFFF;
}

.footer {
  padding: 1rem 0;
  background-color: #000000;
}
.footer p {
  text-align: center;
  font-size: 1.5rem;
  color: #FFFFFF;
}

.transition-fade {
  transition: 0.4s;
  opacity: 1;
}

html.is-animating .transition-fade {
  opacity: 0;
  transform: translateY(-100px);
}