@use 'variables' as v;

html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*,
*:before,
*:after {
    box-sizing: inherit;
}

body {

    &.body-scroll {
        padding-top: 7.68rem;
    }

    font-family: v.$fuente_principal;
    font-size: 1.6rem;
    background-image: linear-gradient(to bottom, v.$Negro 0%, v.$Blanco 100%);
    background-repeat: no-repeat;
    background-attachment: fixed;
}

p {
    font-size: 1rem;
    color: v.$Negro;
    line-height: 1.5;
}

.contenedor {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}

a {
    text-decoration: none;
}

h1, h2, h3 {
    margin: 0 0 5rem 0;
}

h1 {
    font-size: 4rem;
}

h2 {
    font-size: 3.6rem;
}

h3 {
    font-size: 3rem;
}

img {
    max-width: 100%;
    width: 100%;
    height: auto;
    display: block;
}